<template>
    <div>
        <header id="header" style="background:#6BC4DF">
            <div class="header_l">
                <a class="fh" href="javascript:history.back(-1)"> </a>
            </div>
            <h1> </h1>
            <div class="header_r">

            </div>
        </header>
        <section class="dd-xq">
            <ul class="ui-list ui-list-one cp ui-border-t" v-for="item in data.goods">

                <li>
                    <div class="ui-list-thumb">
                        <img :src="item.img_url">
                    </div>
                    <div class="ui-list-info">
                        <div class="ms">
                            <h4 class="ui-nowrap">{{item.name}}</h4>
                            <p class="c-ccc"> {{item.sizeContent}}</p>
                        </div>
                    </div>
                </li>
            </ul>
            <ul class="p10 ui-border-b">
                <li class="mb15 ui-form-textarea ui-border-tb-11 radius5">
                    <label>&nbsp;&nbsp;&nbsp;&nbsp;</label>
                    <textarea placeholder="" maxlength="200"  class="ui-textarea" style="height:45px;" v-model="content"></textarea>
                </li>
            </ul>
            <input value="" class="ui-btn submitOrder" style="margin-bottom: 80px;" @click="addAssessment">
        </section>
    </div>
</template>

<script>
    import {queryOrderById,addAssess} from '../service';
    import { Toast} from 'mint-ui';
    export default {
        name: 'assessment',
        data () {
            return {
                data:{
                    delivery:{},//delivery{{ data.delivery.name}} undefined

                },
                content:''
            }
        },
        computed:{

        },
        methods:{
            addAssessment(){
                if(this.content==''){
                    Toast('');
                    return
                }
                var data={
                    "id":this.$route.params.id,
                    "assessment":this.content
                };
                addAssess(data).then(function (res) {
                    if(res.data.nModified>0 && res.data.ok==1){
                        Toast('')
                    }else{
                        Toast('')
                    }
                })
            }
        },
        mounted(){
            let that=this;
            let _id=that.$route.params.id;
            queryOrderById(_id).then(function (res) {
                that.data=res.data;
            })
        },
        update(){

        },
        beforeRouteUpdate (to, from, next) {
            // 
            //  /foo/:id /foo/1  /foo/2 
            //  Foo 
            //  `this`
//            console.log(to,from)
//            next();

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .t a{margin-top: 10px;}
    .c-999 {
        color: #999;
    }
</style>



